import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);
import MyIndex from "../views/index/index";

const router = new VueRouter({
    linkActiveClass:"active",
    routes:[
        {
            path:"/",
            redirect:{
                name:"index"
            }

        },
        {
            path:"/index",
            component: MyIndex,
            name:"index",
            meta:{
                title:"首页"
            }
        }
    ],
    scrollBehavior(to, from, savedPosition){
        if (savedPosition){
            return savedPosition;

        }
        return {x:0,y:0};
        
    }

});

export default router;