<template>
  <div class="index-container">
    <div v-html="content"></div>
  </div>
</template>

<script>
export default {
    data() {
        return {
          content:"<span>111111111111111111</span>"

        };
    },
    created() {
      this.getContent();
    },
    methods: {
      async getContent (){
        try{
          let res = await this.axios.get("/admin/getdetails",{
            params:{
              // 注册协议
              id:1
            }
          });
          this.content = res.data.content;

        }catch(error){
          console.log(error);
        }
      }
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.word{
  color: red;
}
</style>
